  "name": "Alert",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\nimport { Transition, config } from 'react-spring';\n\nimport styles from './Alert.module.scss';\nimport '../icons';\n\nimport Button from '../Button';\nimport Header from '../Header';\nimport Icon from '../Icon';\nimport Text from '../Text';\n\n/**\n * Alerts are banners that communicate a message with a severity attached to it. They grab the user’s attention to provide critical information needed in context.\n */\nclass Alert extends Component {\n  state = {\n    open: true\n  };\n\n  onClose = ev => {\n    const { onClose } = this.props;\n\n    onClose(ev);\n\n    if (ev.defaultPrevented) return;\n\n    this.setState({\n      open: false\n    });\n  };\n\n  afterClose = () => {\n    const { afterClose } = this.props;\n    setTimeout(afterClose, 1);\n  };\n\n  render() {\n    const {\n      afterClose,\n      children,\n      className,\n      closable,\n      icon,\n      onClose,\n      showIcon,\n      style,\n      title,\n      transitionConfig,\n      type,\n      ...props\n    } = this.props;\n\n    const { open } = this.state;\n\n    const classes = cx(\n      styles.alert,\n      styles[type],\n      {\n        [styles.hasTitle]: title\n      },\n      className,\n      'bui-Alert'\n    );\n\n    const iconName = icon || `bui-${type === 'danger' ? 'error' : type}`;\n\n    return (\n      <Transition\n        items={open}\n        from={{ opacity: 1, transform: 'scale(1)' }}\n        enter={{ opacity: 1, transform: 'scale(1)' }}\n        leave={{ opacity: 0, transform: 'scale(0.5)' }}\n        config={transitionConfig || config.stiff}\n        onDestroyed={this.afterClose}\n      >\n        {show =>\n          show &&\n          (cssStyle => (\n            <div\n              className={classes}\n              style={{ ...style, ...cssStyle }}\n              {...props}\n              aria-live=\"assertive\"\n              role=\"alert\"\n            >\n              {showIcon && (\n                <Text className={styles.iconContainer}>\n                  <Icon icon={iconName} />\n                </Text>\n              )}\n              <Text className={styles.text}>\n                {title && <Header level={5}>{title}</Header>}\n                {children}\n              </Text>\n              {closable && (\n                <Text className={styles.closeContainer}>\n                  <Button icon color=\"flat\" onClick={this.onClose}>\n                    <Icon icon=\"bui-close\" />\n                  </Button>\n                </Text>\n              )}\n            </div>\n          ))\n        }\n      </Transition>\n    );\n  }\n\n  static propTypes = {\n    /** Called when close animation is finished */\n    afterClose: p.func,\n    /** Alert content */\n    children: p.node,\n    /** Classname for the Alert component */\n    className: p.string,\n    /** Is the Alert closable */\n    closable: p.bool,\n    /** Icon for the alert, empty = default icon */\n    icon: p.string,\n    /** Called directly when close button is clicked */\n    onClose: p.func,\n    /** Show icon */\n    showIcon: p.bool,\n    /** Title for the alert empty=no title */\n    title: p.string,\n    /** Type of Alert */\n    type: p.oneOf(['success', 'info', 'warning', 'danger'])\n  };\n\n  static defaultProps = {\n    afterClose: () => null,\n    closable: true,\n    onClose: () => null,\n    showIcon: true,\n    type: 'success'\n  };\n}\n\nexport default Alert;\n",
  "examples": {}
