  "name": "Badge",
  "code": "/* eslint react/display-name: 0 */\n\nimport React from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\nimport { Spring, Transition, config } from 'react-spring';\n\nimport styles from './Badge.module.scss';\n\n/**\n * Badges provide a method to highlight a count of an element either next to it or inside the element itself.\n */\nconst Badge = ({\n  children,\n  className,\n  color,\n  count,\n  countOverflow,\n  showZero,\n  style, // eslint-disable-line react/prop-types\n  title,\n  ...props\n}) => {\n  const classes = cx(\n    styles.badge,\n    styles[color],\n    {\n      [styles.inline]: !children\n    },\n    className,\n    'bui-Badge'\n  );\n  const min = showZero ? 0 : 1;\n  const label = title || count;\n\n  const badge = (\n    <Transition\n      items={count >= min}\n      from={{ scale: 0 }}\n      enter={{ scale: 1 }}\n      leave={{ scale: 0 }}\n      config={config.wobbly}\n    >\n      {show =>\n        show &&\n        (({ scale }) => (\n          <Spring from={{ count }} to={{ count }} config={config.slow}>\n            {({ count }) => {\n              const c = Math.min(countOverflow, Math.round(count));\n              return (\n                <span\n                  className={classes}\n                  role=\"status\"\n                  aria-label={label}\n                  title={label}\n                  style={{\n                    transform: children\n                      ? `translate(50%, -50%) scale(${scale}) `\n                      : null,\n                    ...(style || {})\n                  }}\n                  {...props}\n                >\n                  <span>\n                    {c}\n                    {count > countOverflow && '+'}\n                  </span>\n                </span>\n              );\n            }}\n          </Spring>\n        ))\n      }\n    </Transition>\n  );\n\n  return children ? (\n    <div className={styles.container}>\n      {children}\n      {badge}\n    </div>\n  ) : (\n    badge\n  );\n};\n\nBadge.propTypes = {\n  /** Content Badge attaches to no children = inline */\n  children: p.node,\n  /** Classname for the Badge component */\n  className: p.string,\n  /** Badge color variant */\n  color: p.oneOf(['default', 'success', 'warning', 'danger', 'info']),\n  /** Number to show in badge */\n  count: p.number.isRequired,\n  /** Maximum count to show */\n  countOverflow: p.number,\n  /** Whether to show the Badge when count is zero */\n  showZero: p.bool,\n  /** Text to show when hovering over Badge */\n  title: p.string\n};\n\nBadge.defaultProps = {\n  color: 'default',\n  countOverflow: 99,\n  showZero: false\n};\n\nexport default Badge;\n",
  "examples": {}
