  "code": "import React from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\n\nimport styles from './Button.module.scss';\n\n/**\n * Buttons execute an action or change the state of an application. Button text helps users understand what action will occur when they click or tap.\n */\nconst Button = ({\n  active,\n  children,\n  className,\n  color,\n  disabled,\n  fluid,\n  href,\n  icon,\n  innerRef,\n  outline,\n  size,\n  type,\n  ...props\n}) => {\n  const classes = cx(\n    styles.button,\n    styles[color],\n    styles[size],\n    {\n      [styles.active]: active,\n      [styles.fluid]: fluid,\n      [styles.icon]: icon,\n      [styles.outline]: outline\n    },\n    'bui-Button',\n    className\n  );\n\n  return type === 'link' ? (\n    <a ref={innerRef} className={classes} href={href} role=\"button\" {...props}>\n      <span>{children}</span>\n    </a>\n  ) : (\n    <button\n      ref={innerRef}\n      className={classes}\n      disabled={disabled}\n      type={type}\n      {...props}\n    >\n      {children}\n    </button>\n  );\n};\n\nButton.propTypes = {\n  /** Style button as active state */\n  active: p.bool,\n  /** Button content */\n  children: p.node,\n  /** Classname for the Button component */\n  className: p.string,\n  /** Button color variant */\n  color: p.oneOf([\n    'default',\n    'primary',\n    'success',\n    'info',\n    'warning',\n    'danger',\n    'flat'\n  ]),\n  /** Is the button disabled */\n  disabled: p.bool,\n  /** Display button at full width */\n  fluid: p.bool,\n  /** Link target if button type is link */\n  href: p.string,\n  /** Style button as icon button */\n  icon: p.bool,\n  /** onClick handler */\n  onClick: p.func,\n  /** Style button as outlined (secondary) */\n  outline: p.bool,\n  /** Button size */\n  size: p.oneOf(['sm', 'md', 'lg']),\n  /** Type of button */\n  type: p.oneOf(['button', 'submit', 'reset', 'link'])\n};\n\nButton.defaultProps = {\n  active: false,\n  color: 'default',\n  disabled: false,\n  fluid: false,\n  icon: false,\n  outline: false,\n  size: 'md',\n  type: 'button'\n};\n\nexport default Button;\n",
  "examples": {}
