  "name": "Checkbox",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\nimport { Transition, config } from 'react-spring';\n\nimport styles from './Checkbox.module.scss';\nimport '../icons';\n\nimport Icon from '../Icon';\nimport Text from '../Text';\n\n/**\n * With Checkboxes users can select multiple options in a list\n *\n */\nclass Checkbox extends Component {\n  componentDidMount() {\n    this.setIndeterminate();\n  }\n\n  componentDidUpdate() {\n    this.setIndeterminate();\n  }\n\n  setIndeterminate() {\n    const { indeterminate } = this.props;\n    if (indeterminate) {\n      this.input.checked = false;\n      this.input.indeterminate = true;\n    } else {\n      this.input.indeterminate = false;\n    }\n  }\n\n  setRef = ref => {\n    const { innerRef } = this.props;\n    this.input = ref;\n\n    if (typeof innerRef === 'function') {\n      innerRef(ref);\n    }\n  };\n\n  render() {\n    const {\n      checked,\n      children,\n      className,\n      hasError,\n      indeterminate,\n      innerRef,\n      size,\n      ...props\n    } = this.props;\n\n    const classes = cx(\n      styles.checkbox,\n      styles[size],\n      {\n        [styles.error]: hasError\n      },\n      'bui-Checkbox',\n      className\n    );\n\n    return (\n      <label className={classes}>\n        <input type=\"checkbox\" checked={checked} ref={this.setRef} {...props} />\n        <span className={styles.box}>\n          <Transition\n            items={checked && !indeterminate}\n            from={{ opacity: 0 }}\n            enter={{ opacity: 1 }}\n            leave={{ opacity: 0 }}\n            config={config.stiff}\n          >\n            {show =>\n              show &&\n              (style => (\n                <span className={styles.checked} style={style}>\n                  <Icon icon=\"bui-check\" />\n                </span>\n              ))\n            }\n          </Transition>\n          <Transition\n            items={indeterminate}\n            from={{ opacity: 0 }}\n            enter={{ opacity: 1 }}\n            leave={{ opacity: 0 }}\n            config={config.stiff}\n          >\n            {show =>\n              show &&\n              (style => (\n                <span className={styles.indeterminate} style={style}>\n                  <Icon icon=\"bui-minus\" />\n                </span>\n              ))\n            }\n          </Transition>\n        </span>\n        <Text inline>{children}</Text>\n      </label>\n    );\n  }\n\n  static propTypes = {\n    /** Is the Checkbox checked */\n    checked: p.bool,\n    /** Children to render inside Checkbox label */\n    children: p.node,\n    /** Classname for Checkbox component */\n    className: p.string,\n    /** Is the Checkbox disabled */\n    disabled: p.bool,\n    /** Does the input contains an error */\n    hasError: p.bool,\n    /** id for input */\n    id: p.string,\n    /** Is the Checkbox state indeterminate (overwrites checked) */\n    indeterminate: p.bool,\n    /** name for Checkbox */\n    name: p.string.isRequired,\n    /** Checkbox size */\n    size: p.oneOf(['sm', 'md', 'lg'])\n  };\n\n  static defaultProps = {\n    checked: false,\n    disabled: false,\n    hasError: false,\n    indeterminate: false,\n    size: 'md'\n  };\n}\n\nexport default Checkbox;\n",
  "examples": {}
