  "code": "import React, { Component, createElement } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\n\nimport icons from './library';\nimport styles from './Icon.module.scss';\n\n/* generate unique key for each child element (one time per element) */\nlet keyCounter = 0;\n\nconst renderElement = (\n  { tag, attrs, children } // eslint-disable-line react/prop-types\n) =>\n  createElement(tag, {\n    ...attrs,\n    children: Array.isArray(children)\n      ? children.map(child => {\n          if (!child.attrs.key) {\n            child.attrs.key = keyCounter++;\n          }\n          return renderElement(child);\n        })\n      : null\n  });\n\n/**\n * An Icon represents an action or concept in a visual way.\n *\n * Bui does not come bundled with an icon set. Icons can be manually added or complete sets can be installed from npm:\n *\n * * @barry127/bui-feather-icons\n */\nclass Icon extends Component {\n  render() {\n    const {\n      accessibilityLabel,\n      className,\n      color,\n      icon,\n      size,\n      style,\n      ...props\n    } = this.props;\n\n    const classes = cx(styles.icon, styles[color], 'bui-Icon', className);\n\n    const cssStyle = {\n      fontSize: size,\n      ...style\n    };\n\n    if (!icons[icon] || !icons[icon].icon) {\n      return <span className={cx(classes, styles.none)} style={cssStyle} />;\n    }\n\n    const iconObject = {\n      ...icons[icon].icon,\n      attrs: {\n        ...icons[icon].icon.attrs,\n        role: icons[icon].icon.attrs.role || 'icon',\n        className: cx(classes, icons[icon].icon.attrs.className),\n        'aria-labelledby': accessibilityLabel || icons[icon].name,\n        style: { ...icons[icon].icon.style, ...cssStyle },\n        ...props\n      }\n    };\n\n    return renderElement(iconObject);\n  }\n}\n\nIcon.propTypes = {\n  /** String that clients such as VoiceOver will read to describe the icon */\n  accessibilityLabel: p.string,\n  /** Classname for the Icon component */\n  className: p.string,\n  /** Icon color */\n  color: p.oneOf([\n    'black',\n    'white',\n    'inherit',\n    'red',\n    'red50',\n    'red100',\n    'red200',\n    'red300',\n    'red400',\n    'red500',\n    'red600',\n    'red700',\n    'red800',\n    'red900',\n    'pink',\n    'pink50',\n    'pink100',\n    'pink200',\n    'pink300',\n    'pink400',\n    'pink500',\n    'pink600',\n    'pink700',\n    'pink800',\n    'pink900',\n    'purple',\n    'purple50',\n    'purple100',\n    'purple200',\n    'purple300',\n    'purple400',\n    'purple500',\n    'purple600',\n    'purple700',\n    'purple800',\n    'purple900',\n    'marine',\n    'marine50',\n    'marine100',\n    'marine200',\n    'marine300',\n    'marine400',\n    'marine500',\n    'marine600',\n    'marine700',\n    'marine800',\n    'marine900',\n    'blue',\n    'blue50',\n    'blue100',\n    'blue200',\n    'blue300',\n    'blue400',\n    'blue500',\n    'blue600',\n    'blue700',\n    'blue800',\n    'blue900',\n    'cyan',\n    'cyan50',\n    'cyan100',\n    'cyan200',\n    'cyan300',\n    'cyan400',\n    'cyan500',\n    'cyan600',\n    'cyan700',\n    'cyan800',\n    'cyan900',\n    'teal',\n    'teal50',\n    'teal100',\n    'teal200',\n    'teal300',\n    'teal400',\n    'teal500',\n    'teal600',\n    'teal700',\n    'teal800',\n    'teal900',\n    'green',\n    'green50',\n    'green100',\n    'green200',\n    'green300',\n    'green400',\n    'green500',\n    'green600',\n    'green700',\n    'green800',\n    'green900',\n    'yellow',\n    'yellow50',\n    'yellow100',\n    'yellow200',\n    'yellow300',\n    'yellow400',\n    'yellow500',\n    'yellow600',\n    'yellow700',\n    'yellow800',\n    'yellow900',\n    'orange',\n    'orange50',\n    'orange100',\n    'orange200',\n    'orange300',\n    'orange400',\n    'orange500',\n    'orange600',\n    'orange700',\n    'orange800',\n    'orange900',\n    'darkorange',\n    'darkorange50',\n    'darkorange100',\n    'darkorange200',\n    'darkorange300',\n    'darkorange400',\n    'darkorange500',\n    'darkorange600',\n    'darkorange700',\n    'darkorange800',\n    'darkorange900',\n    'warmgrey',\n    'warmgrey50',\n    'warmgrey100',\n    'warmgrey200',\n    'warmgrey300',\n    'warmgrey400',\n    'warmgrey500',\n    'warmgrey600',\n    'warmgrey700',\n    'warmgrey800',\n    'warmgrey900',\n    'grey',\n    'grey50',\n    'grey100',\n    'grey200',\n    'grey300',\n    'grey400',\n    'grey500',\n    'grey600',\n    'grey700',\n    'grey800',\n    'grey900',\n    'coolgray',\n    'coolgray50',\n    'coolgray100',\n    'coolgray200',\n    'coolgray300',\n    'coolgray400',\n    'coolgray500',\n    'coolgray600',\n    'coolgray700',\n    'coolgray800',\n    'coolgray900'\n  ]),\n  /** Name of the icon to render */\n  icon: p.string.isRequired,\n  /** Icon size */\n  size: p.oneOfType([p.number, p.string]),\n  /** Icon css style */\n  style: p.object\n};\n\nIcon.defaultProps = {\n  color: 'inherit',\n  size: '1em'\n};\n\nexport default Icon;\n",
