  "name": "PasswordInput",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\n\nimport styles from './PasswordInput.module.scss';\nimport '../icons';\n\nimport Button from '../Button';\nimport Icon from '../Icon';\nimport TextInput from '../TextInput/TextInput';\n\n/**\n * PasswordInput is a TextInput to handle passwords\n *\n */\nclass PasswordInput extends Component {\n  state = {\n    visible: false\n  };\n\n  toggleVisible = ev => {\n    const { visible } = this.state;\n\n    this.input.focus();\n    this.setState({ visible: !visible });\n  };\n\n  setRef = ref => {\n    const { innerRef } = this.props;\n    this.input = ref;\n\n    if (typeof innerRef === 'function') {\n      innerRef(ref);\n    }\n  };\n\n  render() {\n    const {\n      children,\n      className,\n      hideReveal,\n      innerRef,\n      size,\n      type,\n      ...props\n    } = this.props;\n    const { visible } = this.state;\n\n    const classes = cx(\n      styles.password,\n      styles[size],\n      {\n        [styles.reveal]: !hideReveal\n      },\n      'bui-PasswordInput',\n      className\n    );\n\n    return (\n      <TextInput\n        className={classes}\n        size={size}\n        type={visible ? 'text' : 'password'}\n        innerRef={this.setRef}\n        {...props}\n      >\n        {!hideReveal && (\n          <Button\n            className={styles.button}\n            color=\"flat\"\n            icon\n            size={size}\n            onClick={this.toggleVisible}\n            tabIndex={-1}\n          >\n            <Icon icon={visible ? 'bui-eye-slash' : 'bui-eye'} />\n          </Button>\n        )}\n        {children}\n      </TextInput>\n    );\n  }\n\n  static propTypes = {\n    /** Children to render inside PasswordInput container */\n    children: p.node,\n    /** Classname for PasswordInput component */\n    className: p.string,\n    /** Is the PasswordInput disabled */\n    disabled: p.bool,\n    /** ErrorMessage for error tooltip, no message = no tooltip */\n    errorMessage: p.oneOfType([p.string, p.func]),\n    /** Position for the error tooltip */\n    errorPosition: p.oneOf([\n      'bottomLeft',\n      'bottom',\n      'bottomRight',\n      'leftTop',\n      'left',\n      'leftBottom',\n      'rightTop',\n      'right',\n      'rightBottom',\n      'topLeft',\n      'top',\n      'topRight'\n    ]),\n    /** Does the password contains an error */\n    hasError: p.bool,\n    /** Hide the eye (reveal) icon button */\n    hideReveal: p.bool,\n    /** Icon for PasswordInput */\n    icon: p.string,\n    /** id for input */\n    id: p.string.isRequired,\n    /** name for PasswordInput */\n    name: p.string.isRequired,\n    /** PasswordInput size */\n    size: p.oneOf(['sm', 'md', 'lg'])\n  };\n\n  static defaultProps = {\n    disabled: false,\n    errorMessage: null,\n    errorPosition: 'bottom',\n    hasError: false,\n    hideReveal: false,\n    size: 'md'\n  };\n}\n\nexport default PasswordInput;\n",
  "examples": {}
