  "name": "RadioGroup",
  "code": "/* eslint-disable react/display-name */\nimport React from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\nimport omit from 'lodash/omit';\n\nimport styles from './RadioGroup.module.scss';\n\nimport RadioButton from './RadioButton';\n\n/**\n * use a RadioGroup when you have a few options a user can choose from\n *\n */\nconst RadioGroup = ({\n  className,\n  inline,\n  name,\n  onChange,\n  options,\n  size,\n  value\n}) => {\n  const classes = cx(\n    styles.group,\n    'bui-RadioGroup',\n    { [styles.inline]: inline },\n    className\n  );\n\n  return (\n    <div className={classes}>\n      {options.map(option => {\n        const { label } = option;\n        const props = omit(option, [\n          'checked',\n          'children',\n          'label',\n          'id',\n          'name',\n          'onChange',\n          'size',\n          'value'\n        ]);\n\n        return (\n          <RadioButton\n            key={option.value}\n            checked={value === option.value}\n            id={`bui-radio-${name}-${option.value}`}\n            name={name}\n            onChange={onChange}\n            size={size}\n            value={option.value}\n            {...props}\n          >\n            {typeof label === 'function' ? label() : label}\n          </RadioButton>\n        );\n      })}\n    </div>\n  );\n};\n\nRadioGroup.propTypes = {\n  /** Classname for RadioGroup component */\n  className: p.string,\n  /** should the group be inline */\n  inline: p.bool,\n  /** name for RadioButtons */\n  name: p.string.isRequired,\n  /** onChange handler for the selected Radio value */\n  onChange: p.func.isRequired,\n  /** List of options for RadioButtons */\n  options: p.arrayOf(\n    p.shape({\n      disabled: p.bool,\n      hasError: p.bool,\n      label: p.oneOfType([p.string, p.func]),\n      value: p.any.isRequired\n    })\n  ),\n  /** RadioGroup size */\n  size: p.oneOf(['sm', 'md', 'lg']),\n  /** current value value */\n  value: p.any.isRequired\n};\n\nRadioGroup.defaultProps = {\n  inline: false,\n  options: [],\n  size: 'md'\n};\n\nexport default RadioGroup;\n",
