  "name": "Select",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\nimport Downshift from 'downshift';\nimport find from 'lodash/find';\n\nimport styles from './Select.module.scss';\nimport '../icons';\n\nimport Button from '../Button';\nimport Icon from '../Icon';\nimport Text from '../Text';\nimport Tooltip from '../Tooltip';\nimport uniqueId from '../../lib/uniqueId';\n\n/**\n * With Select users can select one item from a list of values\n *\n */\nclass Select extends Component {\n  state = {\n    hasFocus: false\n  };\n\n  constructor() {\n    super();\n    this.id = uniqueId();\n    this.tooltipId = uniqueId();\n  }\n\n  componentDidMount() {\n    this.input.addEventListener('focus', this.onFocus);\n    this.input.addEventListener('blur', this.onBlur);\n  }\n\n  componentWillunmount() {\n    this.input.removeEventListener('focus', this.onFocus);\n    this.input.removeEventListener('blur', this.onBlur);\n  }\n\n  focus = () => this.input.focus();\n\n  onBlur = ev => {\n    const { onBlur } = this.props;\n\n    this.setState({ hasFocus: false });\n    if (onBlur) onBlur(ev);\n  };\n\n  onFocus = ev => {\n    const { onFocus } = this.props;\n\n    this.setState({ hasFocus: true });\n    if (onFocus) onFocus(ev);\n  };\n\n  setRef = ref => {\n    const { innerRef } = this.props;\n    this.input = ref;\n\n    if (typeof innerRef === 'function') {\n      innerRef(ref);\n    }\n  };\n\n  render() {\n    const {\n      children,\n      className,\n      errorMessage,\n      errorPosition,\n      filter,\n      hasError,\n      icon,\n      id,\n      innerRef,\n      itemToString,\n      labelId,\n      onChange,\n      onBlur,\n      onFocus,\n      options,\n      renderItem,\n      searchable,\n      size,\n      value,\n      ...props\n    } = this.props;\n\n    const { hasFocus } = this.state;\n\n    const classes = cx(\n      styles.select,\n      styles[size],\n      {\n        [styles.error]: hasError,\n        [styles.icon]: icon\n      },\n      'bui-Select',\n      className\n    );\n\n    if (errorMessage && hasFocus) {\n      props['aria-describedby'] = this.tooltipId;\n    }\n\n    return (\n      <Downshift\n        initialSelectedItem={find(options, { value })}\n        onChange={onChange}\n        itemToString={itemToString}\n        inputId={id}\n      >\n        {({\n          clearSelection,\n          getInputProps,\n          getItemProps,\n          getLabelProps,\n          getMenuProps,\n          getToggleButtonProps,\n          highlightedIndex,\n          inputValue,\n          isOpen,\n          selectedItem\n        }) => {\n          const filteredOptions = searchable\n            ? options.filter(item => filter(item, inputValue))\n            : options;\n\n          return (\n            <div\n              id={this.id}\n              className={cx(classes, {\n                [styles.open]: isOpen && filteredOptions.length > 0\n              })}\n            >\n              <input\n                readOnly={!searchable}\n                ref={this.setRef}\n                {...getInputProps(props)}\n                value={\n                  getInputProps().value ||\n                  '' /* Fix for null value in Downshift */\n                }\n                onClick={searchable ? null : getToggleButtonProps().onClick}\n              />\n              {errorMessage && (\n                <Tooltip\n                  targetId={this.id}\n                  color=\"danger\"\n                  position={errorPosition}\n                  visible={hasFocus && !isOpen}\n                  id={this.tooltipId}\n                >\n                  {typeof errorMessage === 'function'\n                    ? errorMessage()\n                    : errorMessage}\n                </Tooltip>\n              )}\n              {icon && (\n                <div className={styles['icon-container']}>\n                  <Icon\n                    icon={icon}\n                    onClick={\n                      searchable\n                        ? this.focus\n                        : ev => {\n                            getToggleButtonProps().onClick(ev);\n                            this.focus();\n                          }\n                    }\n                  />\n                </div>\n              )}\n              <ul className={styles.list} {...getMenuProps()}>\n                {isOpen &&\n                  filteredOptions.map((item, index) => (\n                    <li {...getItemProps({ index, item })} key={item.value}>\n                      <Text\n                        className={cx(styles.item, {\n                          [styles.highlighted]: highlightedIndex === index,\n                          [styles.selected]: selectedItem === item\n                        })}\n                      >\n                        {renderItem\n                          ? renderItem(item, {\n                              selected: selectedItem === item,\n                              highlighted: highlightedIndex === index,\n                              inputValue\n                            })\n                          : item.value}\n                      </Text>\n                    </li>\n                  ))}\n              </ul>\n              <Button\n                {...getToggleButtonProps({\n                  className: styles.toggleButton,\n                  color: 'flat',\n                  icon: true,\n                  size,\n                  tabIndex: -1\n                })}\n                onClick={ev => {\n                  !isOpen && searchable && clearSelection(ev);\n                  getToggleButtonProps().onClick(ev);\n                  this.focus();\n                }}\n              >\n                <Icon icon={isOpen ? 'bui-chevron-up' : 'bui-chevron-down'} />\n              </Button>\n              {children}\n            </div>\n          );\n        }}\n      </Downshift>\n    );\n  }\n\n  static propTypes = {\n    /** Children to render inside Select container */\n    children: p.node,\n    /** Classname for Select component */\n    className: p.string,\n    /** Is the Select disabled */\n    disabled: p.bool,\n    /** ErrorMessage for error tooltip, no message = no tooltip */\n    errorMessage: p.oneOfType([p.string, p.func]),\n    /** Position for the error tooltip */\n    errorPosition: p.oneOf([\n      'bottomLeft',\n      'bottom',\n      'bottomRight',\n      'leftTop',\n      'left',\n      'leftBottom',\n      'rightTop',\n      'right',\n      'rightBottom',\n      'topLeft',\n      'top',\n      'topRight'\n    ]),\n    /** filter method for searching options, default: (item, inputValue) => !inputValue || item.value.toLowerCase().includes(inputValue.toLowerCase()) */\n    filter: p.func,\n    /** Does the Select contains an error */\n    hasError: p.bool,\n    /** Icon for Select */\n    icon: p.string,\n    /** id for input */\n    id: p.string.isRequired,\n    /** Used for display text, defaults to item => item && item.value */\n    itemToString: p.func,\n    /** name for textInput */\n    name: p.string.isRequired,\n    /** onChange method (selection) => this.setState({value: selection.value}) */\n    onChange: p.func,\n    /** list of options for Select */\n    options: p.any.isRequired,\n    /** Custom renderer for items in dropdown (item, { selected, highlighted, inputValue }) => item */\n    renderItem: p.func,\n    /** Is the options list searchable */\n    searchable: p.bool,\n    /** Select size */\n    size: p.oneOf(['sm', 'md', 'lg']),\n    /** Current value */\n    value: p.any\n  };\n\n  static defaultProps = {\n    disabled: false,\n    errorMessage: null,\n    errorPosition: 'bottom',\n    filter: (item, inputValue) =>\n      !inputValue ||\n      item.value.toLowerCase().includes(inputValue.toLowerCase()),\n    hasError: false,\n    itemToString: item => item && item.value,\n    searchable: false,\n    size: 'md'\n  };\n}\n\nexport default Select;\n",
  "examples": {}
