  "name": "SideNav",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\n\nimport styles from './SideNav.module.scss';\nimport Group from './Group';\nimport Item from './Item';\n\n/**\n * SideNav is a navigation list\n */\nclass SideNav extends Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {\n      resizing: false,\n      width: Math.min(props.maxWidth, Math.max(props.minWidth, props.width))\n    };\n  }\n\n  componentWillUnmount() {\n    document.removeEventListener('mouseup', this.onMouseUp);\n    document.removeEventListener('mousemove', this.onMouseMove);\n  }\n\n  componentDidUpdate(prevProps) {\n    const { width } = this.props;\n\n    if (width !== prevProps.width) {\n      this.setState({ width });\n    }\n  }\n\n  onMouseDown = ev => {\n    const { width } = this.state;\n    const { resizable } = this.props;\n    if (!resizable) return;\n\n    ev.preventDefault();\n\n    this.initialWidth = width;\n    this.initialClientX = ev.clientX;\n\n    document.addEventListener('mouseup', this.onMouseUp);\n    document.addEventListener('mousemove', this.onMouseMove);\n\n    this.setState({ resizing: true });\n  };\n\n  onMouseMove = ev => {\n    const { minWidth, maxWidth } = this.props;\n\n    const width = Math.min(\n      maxWidth,\n      Math.max(minWidth, this.initialWidth - (this.initialClientX - ev.clientX))\n    );\n\n    this.setState({\n      width\n    });\n  };\n\n  onMouseUp = ev => {\n    const { onResize } = this.props;\n    ev.preventDefault();\n\n    this.initialClientX = null;\n    this.initialWidth = null;\n\n    document.removeEventListener('mouseup', this.onMouseUp);\n    document.removeEventListener('mousemove', this.onMouseMove);\n\n    this.setState({ resizing: false });\n\n    onResize(this.state.width);\n  };\n\n  render() {\n    const {\n      children,\n      className,\n      maxWidth,\n      minWidth,\n      onResize,\n      resizable,\n      style,\n      width,\n      ...props\n    } = this.props;\n    const classes = cx(\n      styles.nav,\n      { [styles.resizable]: resizable },\n      'bui-SideNav',\n      className\n    );\n\n    return (\n      <nav\n        className={classes}\n        style={{ ...style, width: this.state.width }}\n        {...props}\n      >\n        <ul className={styles.group}>{children}</ul>\n        <div\n          className={styles.handle}\n          onMouseDown={this.onMouseDown}\n          role=\"presentation\"\n        />\n        {this.state.resizing && <div className={styles.resizing} />}\n      </nav>\n    );\n  }\n\n  static propTypes = {\n    /** Children to render inside SideNav */\n    children: p.node,\n    /** Classname for SideNav component */\n    className: p.string,\n    /** maximum width for SideNav */\n    maxWidth: p.number,\n    /** minimum width for SideNav */\n    minWidth: p.number,\n    /** Resize Handler */\n    onResize: p.func,\n    /** Wether the SideNav is resizable */\n    resizable: p.bool,\n    style: p.object,\n    /** Width of SideNav */\n    width: p.number\n  };\n\n  static defaultProps = {\n    maxWidth: Infinity,\n    minWidth: 128,\n    onResize: () => null,\n    resizable: true,\n    width: 200\n  };\n\n  static Group = Group;\n  static Item = Item;\n}\n\nexport default SideNav;\n",
  "examples": {}
