  "name": "Text",
  "code": "import React from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\n\nimport styles from './Text.module.scss';\n\n/**\n * The Text component is a low level component to handle text.\n *\n */\nconst Text = ({\n  align,\n  children,\n  className,\n  color,\n  highlightLinks,\n  inline,\n  italic,\n  size,\n  style,\n  truncate,\n  underline,\n  weight,\n  ...props\n}) => {\n  const classes = cx(\n    styles.text,\n    styles[align],\n    styles[color],\n    styles[weight],\n    {\n      [styles.highlightLinks]: highlightLinks,\n      [styles.italic]: italic,\n      [styles.truncate]: truncate,\n      [styles.underline]: underline\n    },\n    'bui-Text',\n    className\n  );\n\n  const cssStyle = {\n    fontSize: size,\n    ...style\n  };\n\n  const Tag = inline ? 'span' : 'div';\n  return (\n    <Tag className={classes} style={cssStyle} {...props}>\n      {children}\n    </Tag>\n  );\n};\n\nText.propTypes = {\n  /** Alignment of the text */\n  align: p.oneOf(['left', 'right', 'center', 'justify']),\n  /** Text content */\n  children: p.node,\n  /** Classname for Text component */\n  className: p.string,\n  /** Text color */\n  color: p.oneOf([\n    'black',\n    'white',\n    'inherit',\n    'link',\n    'red',\n    'red50',\n    'red100',\n    'red200',\n    'red300',\n    'red400',\n    'red500',\n    'red600',\n    'red700',\n    'red800',\n    'red900',\n    'pink',\n    'pink50',\n    'pink100',\n    'pink200',\n    'pink300',\n    'pink400',\n    'pink500',\n    'pink600',\n    'pink700',\n    'pink800',\n    'pink900',\n    'purple',\n    'purple50',\n    'purple100',\n    'purple200',\n    'purple300',\n    'purple400',\n    'purple500',\n    'purple600',\n    'purple700',\n    'purple800',\n    'purple900',\n    'marine',\n    'marine50',\n    'marine100',\n    'marine200',\n    'marine300',\n    'marine400',\n    'marine500',\n    'marine600',\n    'marine700',\n    'marine800',\n    'marine900',\n    'blue',\n    'blue50',\n    'blue100',\n    'blue200',\n    'blue300',\n    'blue400',\n    'blue500',\n    'blue600',\n    'blue700',\n    'blue800',\n    'blue900',\n    'cyan',\n    'cyan50',\n    'cyan100',\n    'cyan200',\n    'cyan300',\n    'cyan400',\n    'cyan500',\n    'cyan600',\n    'cyan700',\n    'cyan800',\n    'cyan900',\n    'teal',\n    'teal50',\n    'teal100',\n    'teal200',\n    'teal300',\n    'teal400',\n    'teal500',\n    'teal600',\n    'teal700',\n    'teal800',\n    'teal900',\n    'green',\n    'green50',\n    'green100',\n    'green200',\n    'green300',\n    'green400',\n    'green500',\n    'green600',\n    'green700',\n    'green800',\n    'green900',\n    'yellow',\n    'yellow50',\n    'yellow100',\n    'yellow200',\n    'yellow300',\n    'yellow400',\n    'yellow500',\n    'yellow600',\n    'yellow700',\n    'yellow800',\n    'yellow900',\n    'orange',\n    'orange50',\n    'orange100',\n    'orange200',\n    'orange300',\n    'orange400',\n    'orange500',\n    'orange600',\n    'orange700',\n    'orange800',\n    'orange900',\n    'darkorange',\n    'darkorange50',\n    'darkorange100',\n    'darkorange200',\n    'darkorange300',\n    'darkorange400',\n    'darkorange500',\n    'darkorange600',\n    'darkorange700',\n    'darkorange800',\n    'darkorange900',\n    'warmgrey',\n    'warmgrey50',\n    'warmgrey100',\n    'warmgrey200',\n    'warmgrey300',\n    'warmgrey400',\n    'warmgrey500',\n    'warmgrey600',\n    'warmgrey700',\n    'warmgrey800',\n    'warmgrey900',\n    'grey',\n    'grey50',\n    'grey100',\n    'grey200',\n    'grey300',\n    'grey400',\n    'grey500',\n    'grey600',\n    'grey700',\n    'grey800',\n    'grey900',\n    'coolgray',\n    'coolgray50',\n    'coolgray100',\n    'coolgray200',\n    'coolgray300',\n    'coolgray400',\n    'coolgray500',\n    'coolgray600',\n    'coolgray700',\n    'coolgray800',\n    'coolgray900'\n  ]),\n  /** Should links (a tags) be styled in link color */\n  highlightLinks: p.bool,\n  /** Should the text be inline (<span>) or block (<div>) */\n  inline: p.bool,\n  /** Should text be italic */\n  italic: p.bool,\n  /** Should the text truncate (block only) */\n  truncate: p.bool,\n  /** Should text be underlined */\n  underline: p.bool,\n  /** Text weight */\n  weight: p.oneOf(['bold', 'medium', 'light'])\n};\n\nText.defaultProps = {\n  highlightLinks: false,\n  inline: false,\n  italic: false,\n  truncate: false,\n  underline: false\n};\n\nexport default Text;\n",
