  "name": "TextArea",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\nimport TextareaAutosize from 'react-autosize-textarea';\n\nimport styles from './TextArea.module.scss';\n\nimport Tooltip from '../Tooltip';\nimport uniqueId from '../../lib/uniqueId';\n\n/**\n * Textareas allow for multiline input\n */\nclass TextArea extends Component {\n  state = {\n    hasFocus: false\n  };\n\n  constructor() {\n    super();\n    this.id = uniqueId();\n    this.tooltipId = uniqueId();\n  }\n\n  onBlur = ev => {\n    const { onBlur } = this.props;\n\n    this.setState({ hasFocus: false });\n    if (onBlur) onBlur(ev);\n  };\n\n  onFocus = ev => {\n    const { onFocus } = this.props;\n\n    this.setState({ hasFocus: true });\n    if (onFocus) onFocus(ev);\n  };\n\n  render() {\n    const {\n      children,\n      className,\n      errorMessage,\n      errorPosition,\n      hasError,\n      onBlur,\n      onFocus,\n      size,\n      ...props\n    } = this.props;\n\n    const { hasFocus } = this.state;\n\n    const classes = cx(\n      styles.textarea,\n      styles[size],\n      {\n        [styles.error]: hasError\n      },\n      'bui-TextArea',\n      className\n    );\n\n    if (errorMessage && hasFocus) {\n      props['aria-describedby'] = this.tooltipId;\n    }\n\n    return (\n      <div id={this.id} className={classes}>\n        <TextareaAutosize\n          onBlur={this.onBlur}\n          onFocus={this.onFocus}\n          {...props}\n        />\n        {errorMessage && (\n          <Tooltip\n            targetId={this.id}\n            color=\"danger\"\n            position={errorPosition}\n            visible={hasFocus}\n            id={this.tooltipId}\n          >\n            {typeof errorMessage === 'function' ? errorMessage() : errorMessage}\n          </Tooltip>\n        )}\n        {children}\n      </div>\n    );\n  }\n\n  static propTypes = {\n    /** Children to render inside TextArea container */\n    children: p.node,\n    /** Classname for TextArea component */\n    className: p.string,\n    /** Is the TextArea disabled */\n    disabled: p.bool,\n    /** ErrorMessage for error tooltip, no message = no tooltip */\n    errorMessage: p.oneOfType([p.string, p.func]),\n    /** Position for the error tooltip */\n    errorPosition: p.oneOf([\n      'bottomLeft',\n      'bottom',\n      'bottomRight',\n      'leftTop',\n      'left',\n      'leftBottom',\n      'rightTop',\n      'right',\n      'rightBottom',\n      'topLeft',\n      'top',\n      'topRight'\n    ]),\n    /** Does the TextArea contains an error */\n    hasError: p.bool,\n    /** id for textarea */\n    id: p.string.isRequired,\n    /** maximum number of rows, infinit by default */\n    maxRows: p.number,\n    /** name for textarea */\n    name: p.string.isRequired,\n    /** number of rows */\n    rows: p.number,\n    /** TextArea size */\n    size: p.oneOf(['sm', 'md', 'lg'])\n  };\n\n  static defaultProps = {\n    disabled: false,\n    errorMessage: null,\n    errorPosition: 'bottom',\n    hasError: false,\n    rows: 3,\n    size: 'md'\n  };\n}\n\nexport default TextArea;\n",
  "examples": {}
