  "name": "TextInput",
  "code": "import React, { Component } from 'react';\nimport p from 'prop-types';\nimport cx from 'classnames';\n\nimport styles from './TextInput.module.scss';\n\nimport Icon from '../Icon';\nimport Tooltip from '../Tooltip';\nimport uniqueId from '../../lib/uniqueId';\n\n/**\n * Inputs are the most commonly used form control and allow for text input\n *\n */\nclass TextInput extends Component {\n  state = {\n    hasFocus: false\n  };\n\n  constructor() {\n    super();\n    this.id = uniqueId();\n    this.tooltipId = uniqueId();\n  }\n\n  focus = () => this.input.focus();\n\n  onBlur = ev => {\n    const { onBlur } = this.props;\n\n    this.setState({ hasFocus: false });\n    if (onBlur) onBlur(ev);\n  };\n\n  onFocus = ev => {\n    const { onFocus } = this.props;\n\n    this.setState({ hasFocus: true });\n    if (onFocus) onFocus(ev);\n  };\n\n  setRef = ref => {\n    const { innerRef } = this.props;\n    this.input = ref;\n\n    if (typeof innerRef === 'function') {\n      innerRef(ref);\n    }\n  };\n\n  render() {\n    const {\n      children,\n      className,\n      errorMessage,\n      errorPosition,\n      hasError,\n      icon,\n      innerRef,\n      onBlur,\n      onFocus,\n      size,\n      ...props\n    } = this.props;\n\n    const { hasFocus } = this.state;\n\n    const classes = cx(\n      styles.input,\n      styles[size],\n      {\n        [styles.error]: hasError,\n        [styles.icon]: icon\n      },\n      'bui-TextInput',\n      className\n    );\n\n    if (errorMessage && hasFocus) {\n      props['aria-describedby'] = this.tooltipId;\n    }\n\n    return (\n      <div id={this.id} className={classes}>\n        <input\n          onBlur={this.onBlur}\n          onFocus={this.onFocus}\n          ref={this.setRef}\n          {...props}\n        />\n        {errorMessage && (\n          <Tooltip\n            targetId={this.id}\n            color=\"danger\"\n            position={errorPosition}\n            visible={hasFocus}\n            id={this.tooltipId}\n          >\n            {typeof errorMessage === 'function' ? errorMessage() : errorMessage}\n          </Tooltip>\n        )}\n        {icon && (\n          <div className={styles['icon-container']}>\n            <Icon icon={icon} onClick={this.focus} />\n          </div>\n        )}\n        {children}\n      </div>\n    );\n  }\n\n  static propTypes = {\n    /** Children to render inside TextInput container */\n    children: p.node,\n    /** Classname for TextInput component */\n    className: p.string,\n    /** Is the TextInput disabled */\n    disabled: p.bool,\n    /** ErrorMessage for error tooltip, no message = no tooltip */\n    errorMessage: p.oneOfType([p.string, p.func]),\n    /** Position for the error tooltip */\n    errorPosition: p.oneOf([\n      'bottomLeft',\n      'bottom',\n      'bottomRight',\n      'leftTop',\n      'left',\n      'leftBottom',\n      'rightTop',\n      'right',\n      'rightBottom',\n      'topLeft',\n      'top',\n      'topRight'\n    ]),\n    /** Does the input contains an error */\n    hasError: p.bool,\n    /** Icon for TextInput */\n    icon: p.string,\n    /** id for input */\n    id: p.string.isRequired,\n    /** name for textInput */\n    name: p.string.isRequired,\n    /** TextInput size */\n    size: p.oneOf(['sm', 'md', 'lg']),\n    /** TextInput type */\n    type: p.oneOf(['text', 'email', 'password', 'url', 'search'])\n  };\n\n  static defaultProps = {\n    disabled: false,\n    errorMessage: null,\n    errorPosition: 'bottom',\n    hasError: false,\n    size: 'md',\n    type: 'text'\n  };\n}\n\nexport default TextInput;\n",
  "examples": {}
